import React from 'react';

import { InjectedFormProps, reduxForm } from 'redux-form';

import { OkCancelButtons } from 'components';

import { formNamesConst } from 'consts';
import { ProductRules } from 'containers/ProductDesigner/Products/components';
import { THandleGetProductRule, THandleUpdateProductRule } from 'store';
import { ISelectValue } from 'types';

interface IRulesForm {
  onCancel?: () => void;
  getProductRule: THandleGetProductRule;
  updateProductRule: THandleUpdateProductRule;
  isReadOnly: boolean;
  actionTypesOptions: Array<ISelectValue>;
  initialActionType: string | number;
  isRevolvingCredit: boolean;
  rulesValues: {
    eventId: ISelectValue;
    actionType: ISelectValue;
  };
  isLoading: boolean;
}

type TRulesForm = IRulesForm & InjectedFormProps<{}, IRulesForm>;

const RulesForm: React.FC<TRulesForm> = ({
  handleSubmit,
  onCancel,
  getProductRule,
  updateProductRule,
  dirty,
  pristine,
  rulesValues,
  change,
  actionTypesOptions,
  initialActionType,
  isLoading,
  isReadOnly,
  isRevolvingCredit,
}) => {
  const { eventId, actionType } = rulesValues;

  React.useEffect(
    () => {
      getProductRule();
    },
    [getProductRule]
  );

  const handleGetRule = React.useCallback(
    (fieldName, value) => {
      if (value) {
        change(fieldName, value);

        if (fieldName === 'eventId' && !actionType) {
          change('actionType', actionTypesOptions[0]);
        }

        getProductRule();
      }
    },
    [getProductRule, actionType, change, actionTypesOptions]
  );

  const handleGetRuleByChange = React.useCallback(
    (value: ISelectValue, val: ISelectValue, prevVal: ISelectValue, fieldName: string) => {
      handleGetRule(fieldName, value);
    },
    [handleGetRule]
  );

  // eslint-disable-next-line
  const handleSubmitForm = React.useCallback(
    handleSubmit(updateProductRule),
    [handleSubmit, updateProductRule]
  );

  return (
    <form onSubmit={isReadOnly ? null : handleSubmitForm}>
      <ProductRules
        eventValue={eventId}
        onChangeValues={handleGetRuleByChange as any}
        changeFormField={change}
        handleGetRule={handleGetRule}
        actionTypesOptions={actionTypesOptions}
        initialActionType={initialActionType}
        isLoading={isLoading}
        isReadOnly={isReadOnly}
        dirty={dirty}
      />
      <OkCancelButtons
        okText="Save"
        cancelText="Close"
        onCancel={onCancel}
        withCancelConfirmation={dirty}
        disabledOk={pristine}
        hideOk={isReadOnly}
      />
    </form>
  );
};

export default reduxForm<{}, IRulesForm>({
  form: formNamesConst.PRODUCT_RULES,
  destroyOnUnmount: true,
  enableReinitialize: true,
})(RulesForm);
